<template>
  <div class="app">
    <div class="app__primary">
      <v-app-bar-nav-icon
        color="#fff"
        class="hidden-md-and-up"
        @click="toggleDrawer"
      />

      <div class="app_primary_up">
        <v-btn outlined color="#545454" class="text-capitalize" small>
          <span style="">Login Profesionales</span>
        </v-btn>

        <v-btn outlined color="#545454" class="text-capitalize" small>
          <span>Contáctenos</span>
        </v-btn>


      <v-menu offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-btn outlined class="text-capitalize"
          v-bind="attrs"
          v-on="on"
          small
        >
          Sucursales
        </v-btn>
      </template>
      <v-list dense
      >
        <v-list-item
          v-for="(sucursal, index) in sucursales"
          :key="index"
        >
          <v-list-item-title>{{ sucursal.Nombre }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
        

        <v-btn outlined class="text-capitalize" small>
          <span>Por qué consultar?</span>
        </v-btn>
      </div>

      <div class="app_primary_down" justify-md="end">
        <span v-for="(sucursal, index) in sucursales" :key="index"
          ><v-icon small color="#fff">{{ sucursal.icono }}</v-icon>
          {{ sucursal.Nombre }}: {{ sucursal.Telefono }}
        </span>
      </div>
    </div>

    <slot />
  </div>
</template>

<script>
// Utilities
import { mapGetters, mapMutations, mapState } from "vuex";

export default {
  name: "CoreAppBarBat",

  computed: {
    ...mapGetters(["links"]),
    ...mapState(["sucursales"]),
  },

  methods: {
    ...mapMutations(["toggleDrawer"]),
    onClick(e, item) {
      e.stopPropagation();

      if (item.to || !item.href) return;

      this.$vuetify.goTo(item.href.endsWith("!") ? 0 : item.href);
    },
    imprimeLinks() {
      console.log(this.sucursales);
    },
  },
  created() {
    this.imprimeLinks();
  },
};
</script>


<style>
.app {
  list-style: none;
  margin: 0;
  position: fixed;
  top: 0;
  z-index: 99;
  background: lightcoral;
  width: 100%;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  /* -webkit-flex-flow: row wrap; */
  flex-wrap: nowrap;
  /* height: 30vh; */
  flex-direction: column;
}

.app__primary {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
  width: 100%;
  height: 62px;
  background-color: #545454;
  padding: 0px 200px 0px 200px;
}

.app_primary_up {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  border-bottom: 1px solid #adadad;
  margin: 3px;
}

.app_primary_down {
  display: flex;
  justify-content: flex-end;
  color: #fff !important;
  font-size: 14px;
  margin: 3px;
}
.app_primary_down span {
  margin-left: 15px;
}

.app_primary_up .v-btn {
  color: #adadad !important;
  background-color: transparent !important;
  border: 0px solid #adadad !important;
}

.app_primary_up .v-select {
  color: #adadad !important;
  margin: 0px;
  border: transparent;
}

@media all and (max-width: 1127px) {
  .app__primary {
    padding: 0px 20px 0px 20px;
  }
}

@media all and (max-width: 970px) {
  .app_primary_down, .app_primary_up {
    display: none !important;
  }
  .app__primary {
    padding: 0px 20px 0px 20px;
    align-items: flex-start;
    justify-content: center;
  }
}
</style>


